import * as React from 'react';
import { ItemKey, ItemSlot, RenderToolbar, SlotType, TextTypeSingleLineDefaultWidth } from '@ali/4ever-component';
import HeadingButton from './components/next/headingButton';
import HeadingGroup, { getHeadingGroupWidth } from './components/next/headingGroup';

export default function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {


  return (props, controller) => {

    const { buttonName, extraProps } = props;

    switch (buttonName) {
      case ItemKey.heading:
        return (
          <ItemSlot display={SlotType.textSelect} width={TextTypeSingleLineDefaultWidth}>
            <HeadingButton
              controller={controller}
              locale={locale}
              {...extraProps}
            />
          </ItemSlot>
        )
      case ItemKey['heading.group']:
        return (
          <ItemSlot display={SlotType.custom} width={getHeadingGroupWidth}>
            <HeadingGroup
              controller={controller}
              locale={locale}
              {...extraProps}
            />
          </ItemSlot>
        )

      default:
        break;
    }


    return null
  };
}
